@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,600,700,800);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.modal-content { border-radius: 0; box-shadow: none; border: none; }
.modal-header { position: relative; display: block; padding: 18px 40px 20px; color: #fff; background-color: #303030; border-radius: 0; }
.modal-header .closeButton { position: absolute; top: 10px; right: 10px; font-size: 13px; }
.modal-header .closeButton:hover { color: #000; }
.modal-title { font-size: 28px; font-weight: 800; color: #fefefe; }
.modal-title + p { margin-top: -7px; font-size: 20px; }
p:last-child { margin-bottom: 0; }
.modal-body { padding: 20px 40px; background-color: #fefefe; }
.modal-body .estimate-container { padding-top: 17px; }
.modal-body .estimate-text div.estimate-title { margin-bottom: 10px; font-size: 18px; font-weight: 700; }
.modal-body .estimate-text .radio input[type=radio] { position: absolute; }
.modal-body .estimate-text .radio label { position: relative; top: -2px; padding-left: 20px; font-size: 14px; font-style: italic; }
.modal-body .estimate-text button[type=submit] { margin-top: 15px; }

.form-controls input,
.form-controls textarea,
.form-controls select {
  width: 100%;
  min-height: 41px;
  margin-top: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
  color: #999999;
  border: 1px solid #aaa;
  font-size: 13px;
  text-indent: 15px;
  resize: none;
}
.form-controls .case-category select,
.form-controls .budget select {
  padding: 11px 0 10px;
  -webkit-appearance: none;
  -moz-appearance: none;
  border: 1px solid #aaa;
  border-radius: 0;
}

div.wpcf7-response-output { margin: 1em 0 0; color: #fefefe; }
div.wpcf7-mail-sent-ok { background-color: #398f14; }
div.wpcf7-validation-errors, div.wpcf7-acceptance-missing { background-color: #c82333; border: 2px solid #c82333; }
.wpcf7-not-valid { border-color: #c82333 !important; }
span.wpcf7-not-valid-tip { padding: 3px 0 0 16px; font-size: 13px; color: #c82333; }

@media (max-width: 576px) {
  .modal-title {
    margin-top: 40px;
  }
}
body { font-family: 'Raleway', sans-serif; }
.btn { border-radius: 0; }
.btn-outline-light.focus, .btn-outline-light:focus {
  box-shadow: none !important;
}
.clearfix { clear: both; height: 0; line-height: 0; }

#background { height: 100%; width: 100%; position: fixed; top: 0; left: 0; background: #000 url(/my-resume-react/static/media/bg.8dc781fe.jpg) no-repeat; background-size: cover; background-position: center center;}

header { position: fixed; top: 0; left: 0; bottom: 0; width: 33.333333%; background-color: rgba(0,0,0,0.85); overflow: hidden; border-right: 20px solid rgba(255,255,255,0.8); }
header .vCard { margin-top: 45%; }
header hr { border-color: rgba(255,255,255,0.5); }

.col-right { width: 100%; padding-left: 33.333333%; }
#hi { font-size: 16px; line-height: 16px; font-weight: 400; color: #fefefe;}
#fullName { font-size: 40px; font-weight: 800; line-height: 40px; color: #fefefe; text-transform: uppercase; }
#jobTitle { font-size: 20px; font-weight: 700; line-height: 20px; color: #fefefe; text-transform: uppercase; }
#objTitle { margin: 20px 0 10px; font-size: 20px; font-weight: 700; line-height: 20px; color: #ccc; }
#objective { font-size: 16px; font-weight: 400; line-height: 20px; color: #ccc;}
#hireMe { margin: 20px 0; }
#download a { float: right; font-size:16px; color: #999; }
#sectionTitle { font-size: 40px; font-weight: 800; line-height: 150px; }
#navContainer { display: block; padding-left: 20px; width: 100%; }
#nav { float: right; text-align: right; margin-top: 50px; margin-right: 20px; }
#nav svg { position: absolute; top: 15px; left: 15px; }
#nav .btn { position: relative; width: 100px; height: 100px; margin-left: 5px; font-size: 12px; }
#nav .btn .title { position: absolute; bottom: 13px; text-transform: uppercase; }
#nav .btn .plus { position: absolute; bottom: 0px; right: 5px; font-size: 30px; }
.collapseContentRow { padding-left: 30px; margin-top: 30px; width: 100%; }
#contactTitle { font-size: 40px; font-weight: 700; color: #fefefe; line-height: 40px; }
#bringTitle { margin-top: 20px; font-size: 20px; font-weight: 700; color: #fefefe;line-height: 20px; }
#bringList { list-style-type: none; margin: 20px 0; padding: 0;font-size: 16px; font-weight: 400; line-height: 20px; color: #ccc; }
.contactBtn { margin-top: 10px; }
form.wpcf7-form { margin-bottom: 20px; }

@media (min-width: 577px) {
  header { height: 100% !important; }
  #objTitle, #objective { display: block !important; }
}

@media (min-width: 577px) and (max-width: 767px) {
  header { width: 50% !important; }
  .col-right { padding-left: 50% !important; }
}

@media (max-width: 576px) {
  header { position: relative; width: 100%; height: 100%; border-right: 0; padding-bottom: 20px; }
  header .vCard { margin-top: 75px; max-width: 100%; }
  #mobileNavButton { position: absolute; z-index: 5; right: 10px; top: 10px; }
  #mobileNav { display: none; width: 75%; background-color: #fefefe; color: #000; position: absolute; z-index: 20; left: 100%; }
  #mobileNavClose { margin: 10px 10px 0; }
  #mobileNav ul { clear: both; list-style-type: none; margin: 0; padding: 0; position: relative; top: 15px; }
  #mobileNav ul li { font-size: 30px; text-align: center; color: #666; text-transform: uppercase; }
  .col-right { display: none; padding-left: 0; }
  .collapseContentRow { padding-left: 0; }
  #contactTitle { font-size: 30px; }
}
